.video {
    padding: 90px 0 100px;
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 30px 0;
        position: relative;
    }
    &__title {
        margin-bottom: 60px;
    }
    &__link {
        position: absolute;
        padding-left: 60px;
        &::before {
            content: '';
            position: absolute;
            width: 15px;
            height: 15px;
            left: 35px;
            top: 50%;
            background: url('../img/play-solid.svg') no-repeat center;
            transform: translate(0%, -50%);
        }
    }
}

@media (max-width: 420px) {
    .video {
        &__title {
            margin-bottom: 30px;
        }
        &__link {
            font-size: 0;
            height: 50px;
            width: 70px;
            &::before {
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
}
