:root {
    font-size: calc(0.5vw + 0.4em);
}

html,
body {
    height: 100%;
}

body {
    @extend %poppins-400;
    display: flex;
    flex-direction: column;
}

main {
    flex-grow: 1;
}

.container {
    max-width: 1330px;
    padding: 0 15px;
    margin: 0 auto;
}

.title {
    color: $title-color;
    font-weight: 400;
    @extend %poppins-400;
    font-size: 4.375rem;
    line-height: 4.8125rem;
}

.sup-title {
    color: $title-color;
    font-weight: 400;
    @extend %poppins-400;
    font-size: 5rem;
    line-height: 5.5rem;
}

.subtitle {
    @extend %prompt-400;
    color: $title-color;
    font-size: 1.875rem;
    line-height: 2.625rem;
}

.button {
    font-size: 18px;
    padding: 1.125em 2.25em;
    background: $btn-color;
    border-radius: 0.3125em;
    @extend %poppins-400;
    color: $btn-text;
    cursor: pointer;
    border: 2px solid transparent;
    transition: border .3s ease-in;
    &:hover {
        border: 2px solid rgba(255, 255, 255, .4);
    }
}

.btn {
    @extend %poppins-400;
    font-size: 18px;
    color: $title-color;
    padding: 1.125em 2.25em;
    background: rgba(255, 255, 255, 0.4);
    border: 2px solid rgba(255, 255, 255, 0.4);
    box-shadow: 0px 4px 20px rgba(193, 193, 193, 0.3);
    border-radius: 0.3125em;
    cursor: pointer;
}

.glass-bg {
    padding: 45px 52px 52px;
    border: 2px solid rgba(255, 255, 255, 0.4);
    background: rgba(255, 255, 255, 0.4);
    box-shadow: 0px 4px 20px rgba(193, 193, 193, 0.3);
    border-radius: 10px;
}

.text {
    @extend %prompt-400;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
}

.subtitle-h3 {
    @extend %poppins-400;
    font-weight: 400;
    font-size: 34px;
    line-height: 38px;
    color: $title-color;
}

.link {
    display: inline-block;
    padding-left: 24px;
    height: 26px;
    width: 192px;
    font-size: 1.125rem;
    line-height: 1.5625rem;
    text-transform: uppercase;
    position: relative;
    color: $title-color;
    cursor: pointer;
    &::before {
        content: '';
        position: absolute;
        height: 14px;
        width: 14px;
        background: #EA5959;
        left: 0;
        top: 6px;
    }
}

.slick-arrow {
    border: none;
    background-color: $arrows-bg;
    box-shadow: 0px 4px 20px rgba(193, 193, 193, 0.3);
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 10px;
}

.slick-disabled {
    path {
        fill-opacity: .4;
    }
}

@media (max-width: 1024px) {
     :root {
        font-size: 0.75em;
    }
    .subtitle {
        font-size: 1.83rem;
        line-height: 2rem;
    }
}

@media (max-width: 735px) {
    .text {
        font-size: 16px;
        line-height: 20px;
    }
}

@media (max-width: 420px) {
    .btn {
        font-size: 12px;
        padding: 0.85em 1.5em;
    }
    .title {
        font-size: 3rem;
        line-height: 3.5rem;
    }
    .subtitle-h3 {
        font-size: 28px;
        line-height: 30px;
    }
    .button {
        font-size: 12px;
        padding: 0.85em 1.5em;
    }
}
