.questions {
    padding: 90px 0 100px;
    &__title {
        margin-bottom: 60px;
    }
    .acc-head {
        position: relative;
        padding: 45px 10px 45px 135px;
        cursor: pointer;
        @extend %poppins-400;
        color: $text-color;
        font-size: 44px;
        line-height: 50px;
        &::before {
            position: absolute;
            content: "";
            background-image: url("../img/arrow.svg");
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center center;
            width: 45px;
            height: 45px;
            left: 25px;
            top: 50%;
            transform: translateY(-50%);
            transition: 0.3s;
        }
    }
    .in.acc-head::before {
        transform: rotate(180deg) translateY(50%);
    }
    .acc-body {
        padding: 10px;
        margin-bottom: 5px;
        display: none;
    }
}

@media (max-width:735px) {
    .questions {
        .acc-head {
            font-size: 2rem;
            line-height: 2.5rem;
            padding: 25px 10px 25px 65px;
            &::before {
                width: 25px;
                height: 25px;
            }
        }
    }
}
