.numbers {
    position: relative;
    padding: 90px 0 100px;
    z-index: -2;
    &::after {
        content: "";
        position: absolute;
        height: 842px;
        width: 850px;
        left: 0;
        top: -90px;
        background-image: url('../img/numbers-bg.png');
        background-repeat: no-repeat;
        background-position: center right;
        background-size: contain;
        z-index: -1;
    }
    &__wrapper {
        margin-top: 135px;
        display: flex;
        justify-content: space-between;
        z-index: -3;
    }
    &__title {
        font-size: 70px;
        line-height: 77px;
        color: $text-color;
    }
    &__item {
        padding: 30px 0;
        border-top: 2px solid $top-slider-bg;
        position: relative;
    }
    &__item-title {
        font-size: 30px;
        line-height: 42px;
    }
    &__item-text {
        margin-top: 20px;
        color: $text-color;
        max-width: 383px;
    }
    &__item-num {
        display: block;
        font-size: 80px;
        line-height: 112px;
        position: absolute;
        right: 0;
        top: 16px;
    }
    &__right-side {
        max-width: 770px;
        width: 100%;
    }
}

@media (max-width: 1024px) {
    .numbers {
        &::after {
            display: none;
        }
        &__wrapper {
            flex-direction: column;
            gap: 50px;
            margin-top: 40px;
        }
    }
}

@media (max-width: 735px) {
    .numbers {
        &__item-num {
            position: relative;
            font-size: 50px;
            line-height: 80px;
            text-align: center;
        }
    }
}

@media (max-width: 420px) {
    .numbers {
        &__title {
            font-size: 3rem;
            line-height: 3.5rem;
        }
    }
}
