    .comments {
        padding: 90px 0 100px;
        &__title {
            margin-bottom: 60px;
        }
        &__wrap {
            padding-left: 110px;
            padding-right: 110px;
            padding-bottom: 140px;
            position: relative;
            &::after {
                content: '';
                position: absolute;
                width: 217px;
                height: 217px;
                right: 150px;
                bottom: -50px;
                background-image: url('../img/comments-bg.png');
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
                z-index: -3;
            }
        }
        &__item {
            display: grid;
            grid-template-columns: 200px repeat(5, 1fr);
            grid-template-rows: 200px 1fr;
            column-gap: 50px;
            row-gap: 30px;
        }
        &__item-foto {
            border-radius: 50%;
            grid-area: 1/1/2/2;
        }
        &__social-list {
            grid-area: 2/1/3/2;
            display: flex;
            justify-content: center;
        }
        &__social-item:not(:first-child) {
            margin-left: 20px;
        }
        &__item-desc {
            grid-area: 1/2/2/4;
            justify-self: flex-start;
            align-self: center;
            @extend %prompt-400;
        }
        &__item-name {
            font-size: 39px;
            line-height: 56px;
            color: $title-color;
        }
        &__item-prof {
            font-size: 18px;
            line-height: 25px;
            color: $inactive;
        }
        &__item-stars {
            grid-area: 1/5/2/7;
            justify-self: flex-end;
            align-self: center;
        }
        &__item-text {
            grid-area: 2/2/3/7;
            font-size: 30px;
            line-height: 42px;
            color: $text-color;
        }
        .slick-prev,
        .slick-next {
            position: absolute;
            bottom: -80px;
        }
        .slick-prev {
            position: absolute;
            right: 50px;
        }
        .slick-next {
            position: absolute;
            right: 0;
        }
    }
    
    @media (max-width: 1024px) {
        .comments {
            &__wrap {
                padding: 0 50px 100px 50px;
                &::after {
                    display: none;
                }
            }
            &__item {
                grid-template-columns: 200px repeat(2, 1fr);
                grid-template-rows: 200px 1fr 1fr;
                gap: 30px;
            }
            &__item-foto {
                grid-area: 1/1/2/2;
            }
            &__social-list {
                grid-area: 1 / 2 / 2 / 4;
                justify-content: flex-start;
            }
            &__item-desc {
                grid-area: 1/2/2/4;
                justify-self: flex-start;
                align-self: center;
            }
            &__item-prof {
                font-size: 18px;
                line-height: 25px;
                color: $inactive;
                align-self: flex-end;
            }
            &__item-stars {
                grid-area: 1 / 2 / 2 / 4;
                justify-self: flex-start;
                align-self: flex-end;
            }
            &__item-text {
                grid-area: 2/1/4/4;
            }
        }
    }
    
    @media (max-width: 735px) {
        .comments {
            &__wrap {
                padding: 10px 10px 10px 10px;
            }
            &__item {
                grid-template-columns: 1fr;
                grid-template-rows: 300px 150px 1fr;
                column-gap: 0;
                row-gap: 30px;
            }
            &__item-left {
                grid-area: 1/1/2/2;
            }
            &__item-foto {
                max-width: 100%;
                height: 300px;
                margin: 0 auto;
            }
            &__social-list {
                grid-area: 2 / 1 / 3 / 2;
                justify-content: center;
            }
            &__item-name {
                align-self: center;
                font-size: 25px;
                line-height: 30px;
            }
            &__item-desc {
                grid-area: 2 / 1 / 3 / 2;
                justify-self: center;
                align-self: center;
            }
            &__item-prof {
                font-size: 16px;
                line-height: 20px;
                color: $inactive;
                align-self: flex-end;
            }
            &__item-stars {
                grid-area: 2 / 1 / 3 / 2;
                justify-self: center;
                align-self: flex-end;
            }
            &__item-text {
                grid-area: 3/1/4/3;
                font-size: 18px;
                line-height: 23px;
            }
        }
    }
    
    @media (max-width: 420px) {
        .comments {
            &__item {
                grid-template-rows: 200px 150px 1fr;
            }
            &__item-foto {
                height: 200px;
            }
        }
    }
