.features {
    position: relative;
    z-index: -2;
    padding: 90px 0 100px;
    &::after {
        content: "";
        position: absolute;
        height: 638px;
        width: 1397px;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url('../img/features-bg.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        z-index: -1;
        max-width: 100%;
    }
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: 60px;
    }
    &__title {
        max-width: 500px;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr;
        border: 1px solid rgba(255, 255, 255, 0.6);
        margin-bottom: 143px;
    }
    &__item {
        padding: 45px 50px;
    }
    &__item+.features__item {
        border-left: 2px solid $top-slider-bg;
    }
    &__item-title {
        margin-bottom: 23px;
    }
    &__item-text {
        margin-bottom: 30px;
        max-width: 280px;
    }
    &__item-point {
        display: flex;
        align-items: center;
        gap: 22px;
    }
    &__item-point+.features__item-point {
        margin-top: 15px;
    }
    &__item-icon {
        flex-basis: 30px;
    }
    &__item-descr {
        flex-basis: 210px;
    }
}

@media (max-width: 1024px) {
    .features {
        &::after {
            display: none;
        }
        &__list {
            display: block;
            margin-bottom: 0;
        }
        &__item {
            padding: 25px 30px;
        }
        &__item+.features__item {
            border-left: none;
            border-top: 2px solid $top-slider-bg;
        }
        &__item-text {
            max-width: 100%;
        }
    }
}

@media (max-width: 735px) {
    .features {
        &__btn {
            display: none;
        }
    }
}
