.footer {
    background-color: $title-color;
    color: $bg-color;
    &__wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 20px;
        padding: 85px 0 67px;
    }
    &__left-side {
        display: flex;
        flex-direction: column;
    }
    .logo {
        padding-bottom: 38px;
    }
    &__adress {
        color: $bg-color;
    }
    &__link {
        font-size: 18px;
        line-height: 25px;
        padding: 10px 20px;
    }
    &__text {
        margin-bottom: 7px;
    }
    &__subtext {
        font-size: 14px;
        line-height: 20px;
    }
    &__copy {
        margin-top: auto;
    }
    &__menu {
        padding: 58px 0;
        display: flex;
        flex-direction: column;
    }
    &__menu-list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
    }
    &__social-link {
        svg {
            fill: #ffffff;
        }
    }
    &__social-list {
        display: flex;
        justify-content: space-evenly;
        align-items: flex-end;
        margin-top: 40px;
    }
    &__social-item:hover .footer__social-link svg {
        fill: $btn-color;
    }
}

@media (max-width: 735px) {
    .footer {
        &__wrap {
            grid-template-columns: 1fr;
            grid-template-rows: repeat(2, 1fr);
        }
        &__left-side {
            grid-area: 2 / 1 / 3 / 2;
        }
        &__menu {
            grid-area: 1 / 1 / 2 / 2;
        }
        &__copy {
            margin-top: 0;
        }
    }
}

@media (max-width: 420px) {
    .footer {
        &__menu-list {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}
