.price {
    padding: 90px 0 100px;
    &__title {
        margin-bottom: 60px;
    }
    &__wrapper {
        margin-bottom: 90px;
    }
    &__list {
        display: flex;
        gap: 20px;
    }
    &__item-wrapper {
        transition: border .3s ease-in;
        &:hover {
            border: 2px solid $btn-color;
        }
    }
    &__item-title {
        margin-bottom: 46px;
    }
    &__item-tips {
        margin-bottom: 33px;
    }
    &__item-tip {
        @extend %prompt-400;
        font-size: 18px;
        line-height: 25px;
        color: $title-color;
        position: relative;
    }
    &__item-tip+.price__item-tip {
        margin-top: 11px;
    }
    .price-checkbox {
        margin-right: 10px;
    }
    .inactive {
        color: $inactive;
        svg {
            fill: $inactive;
        }
    }
    &__item-bottom {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        gap: 25px;
    }
    &__item-number {
        @extend %prompt-400;
        font-size: 79px;
        line-height: 88px;
        color: $text-color;
        display: flex;
    }
    .price__value {
        position: relative;
        &::after {
            content: '/ month';
            position: absolute;
            color: $inactive;
            width: 80px;
            height: 26px;
            right: -100%;
            bottom: 0;
            font-size: 18px;
            line-height: 25px;
        }
    }
}

@media (max-width: 1024px) {
    .price {
        &__title {
            margin-bottom: 30px;
        }
        &__wrapper {
            margin-bottom: 0;
        }
    }
}

@media (max-width: 735px) {
    .price {
        &__list {
            flex-direction: column;
        }
        &__item-bottom {
            align-items: flex-start;
        }
        &__item-btn {
            align-self: stretch;
            text-align: center;
        }
    }
}

@media (max-width: 420px) {
    .price {
        &__item-wrapper {
            padding: 25px 30px;
        }
        &__item-tip {
            font-size: 14px;
            line-height: 20px;
        }
        &__item-number {
            font-size: 50px;
            line-height: 58px;
        }
        .price__value {
            &::after {
                right: -140%;
            }
        }
    }
}
