.technologies {
    padding: 90px 0 100px;
    &__wrapper:nth-child(odd) .technologies__box-content {
        margin-left: auto;
    }
    &__wrapper:nth-child(odd) .technologies__title {
        text-align: left;
    }
    &__wrapper:nth-child(even) .technologies__box-content {
        margin-left: 0;
    }
    &__wrapper:nth-child(even) .technologies__title {
        text-align: right;
    }
    &__title {
        margin-bottom: 60px;
    }
    &__box-content {
        max-width: 700px;
    }
    &__subtitle {
        margin-bottom: 53px;
    }
    &__text+.technologies__text {
        margin-top: 20px;
    }
    &__link {
        margin: 50px 0;
    }
    .box-1,
    .box-2 {
        background-repeat: no-repeat;
        background-size: 40%;
    }
    .box-1 {
        background-position: 0 100%;
        background-image: url('../img/technologies-bg-1.png');
    }
    .box-2 {
        background-position: 100% 0;
        background-image: url('../img/technologies-bg-2.png');
    }
}
