.team {
    padding: 90px 0 100px;
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
    &__title {
        max-width: 50%;
    }
    &__button:nth-child(2) {
        margin-left: 30px;
    }
    &__list-wrapper {
        padding: 80px 0 0;
    }
    &__list {
        position: relative;
    }
    &__person {
        margin-bottom: 40px;
    }
    &__person-foto {
        margin: 0 auto 15px;
        border-radius: 50%;
        overflow: hidden;
        width: 220px;
        height: 220px;
    }
    &__person-name,
    .team__person-prof {
        text-align: center;
    }
    &__person-name {
        margin-bottom: 12px;
    }
    .slick-slide {
        margin-right: 40px;
    }
    .slick-prev,
    .slick-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
    }
    .slick-prev {
        position: absolute;
        left: 0;
    }
    .slick-next {
        position: absolute;
        right: 0;
    }
}

@media (max-width: 1024px) {
    .team {
        &__top {
            flex-direction: column;
            gap: 60px;
        }
        &__title {
            max-width: max-content;
        }
        .slick-slide {
            margin-right: 50px;
        }
    }
}

@media (max-width: 735px) {
    .team {
        &__top-buttons {
            text-align: center;
            width: 100%;
        }
        &__button {
            display: block;
        }
        &__button:nth-child(2) {
            margin-left: 0;
            margin-top: 30px;
        }
    }
}
