.contacts {
    padding: 90px 0 100px;
    &__title {
        margin-bottom: 60px;
    }
    &__wrap {
        position: relative;
        &::before,
        &::after {
            content: '';
            position: absolute;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            z-index: -1;
        }
        &::before {
            background-image: url('../img/contacts-bg1.png');
            width: 423px;
            height: 423px;
            left: -200px;
            top: -210px;
        }
        &::after {
            width: 236px;
            height: 236px;
            background-image: url('../img/contacts-bg2.png');
            right: -14px;
            top: 0;
        }
    }
    &__form {
        max-width: 640px;
        margin-left: auto;
        color: $text-color;
        // position: relative;
    }
    &__input {
        display: block;
        padding: 15px 32px;
        width: 100%;
        margin-bottom: 18px;
        color: $inactive;
        outline-color: transparent;
        border: 3px solid transparent;
        transition: all .3 ease-in;
    }
    &__input:focus {
        border: 3px solid $outline;
    }
    &__label {
        margin-bottom: 10px;
    }
    &__textarea {
        width: 100%;
        padding: 15px 32px;
        margin-bottom: 50px;
        resize: none;
        outline-color: transparent;
        border: 3px solid transparent;
        transition: all .3 ease-in;
    }
    &__textarea:focus {
        border: 3px solid $outline;
    }
    &__submit {
        margin-bottom: 50px;
        display: block;
        width: 100%;
        @extend %prompt-400;
        font-weight: 700;
    }
    &__text-box {
        position: absolute;
        max-width: calc(100% - 650px);
        left: 0;
        bottom: 0;
        padding: 0 20px 0 20px;
    }
    &__text:first-child {
        margin-bottom: 20px;
    }
    &__text:first-child::before {
        content: "";
        position: absolute;
        width: 12px;
        height: 10px;
        background-image: url("../img/point.png");
        left: 0;
        top: 6px;
    }
    &__text:nth-child(2) {
        color: $inactive;
    }
}

@media (max-width: 1024px) {
    .contacts {
        &__form {
            margin-left: 0;
        }
        &__text-box {
            position: relative;
            max-width: 100%;
            padding: 0;
            padding-left: 15px;
            margin-bottom: 20px;
            &::before {
                content: "";
                position: absolute;
                width: 12px;
                height: 10px;
                background-image: url("../img/point.png");
                left: 0;
                top: 6px;
            }
        }
        &__text {
            &::before {
                display: none;
            }
        }
    }
}

@media (max-width: 735px) {
    .contacts {
        &__wrap {
            &::before,
            &::after {
                display: none;
            }
        }
    }
}
