.top-slider {
    position: relative;
    background-color: $top-slider-bg;
    z-index: -3;
    &__wrapper {
        padding: 50px 0;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        height: 180px;
        z-index: -1;
    }
    &__item {
        text-align: center;
    }
}

@media (max-width: 1024px) {
    .top-slider {
        &__wrapper {
            padding: 20px 0;
        }
    }
}

@media (max-width: 735px) {
    .top-slider {
        display: none;
    }
}
