/* Header */

.header {
    padding-bottom: 7.1875rem;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        background-image: url("../img/top-bg.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        width: 53.125rem;
        height: 60.125rem;
        z-index: -2;
    }
    &__wrapper {
        display: grid;
        padding: 16px;
        grid-template-columns: max-content max-content max-content;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 6.75rem;
        .btn {
            text-decoration: none;
            color: #000000;
            border: 2px solid transparent;
            cursor: pointer;
            padding: 16px 25px;
            border-radius: 5px;
        }
        .logo {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .menu {
            background-color: transparent;
            &__button {
                display: none;
            }
            &__list {
                display: grid;
                grid-auto-flow: column;
                column-gap: 24px;
                margin: 0;
                padding: 0;
                list-style: none;
            }
            &__list .header__link {
                display: none;
            }
            &__link {
                color: $title-color;
                align-self: center;
                text-decoration: none;
            }
        }
    }
    &__inner {
        display: flex;
        flex-direction: column;
        gap: 55px;
    }
    &__title {
        max-width: 50vw;
    }
    &__button {
        align-self: flex-start;
    }
    &__video-link {
        text-transform: uppercase;
        margin-top: 100px;
    }
}

@media (max-width: 1024px) {
    .header {
        &::after {
            display: none;
        }
        &__wrapper {
            // padding-left: 24px;
            // padding-right: 24px;
            grid-template-columns: max-content max-content;
            align-items: flex-start;
            .menu__list .header__link {
                display: block;
                padding: 16px 25px;
                text-align: center;
            }
            .header__logo {
                z-index: 2;
                align-self: start;
            }
            .header__menu {
                position: absolute;
                top: 0;
                right: 0;
                left: 0;
            }
            .header__link {
                display: none;
            }
            .header__logo {
                justify-content: start;
                align-self: flex-start;
                margin-left: -14px;
            }
            .menu {
                display: grid;
                &__button {
                    z-index: 1;
                    display: block;
                    margin: 0;
                    margin-top: 16px;
                    margin-right: 16px;
                    padding: 0;
                    border: none;
                    background-color: transparent;
                    justify-self: end;
                }
                &__list {
                    display: none;
                    grid-auto-flow: row;
                    row-gap: 8px;
                    padding-top: 35px;
                    padding-bottom: 35px;
                    padding-left: 64px;
                    padding-right: 64px;
                    text-align: center;
                    background-color: #fff;
                }
                &__list--open {
                    display: grid;
                }
                &__link {
                    display: block;
                    padding: 10px 0;
                }
                &__btn {
                    margin: 0 auto;
                }
            }
            .menu__icon {
                display: block;
                stroke: #000000;
            }
            .menu__icon:hover {
                stroke: #454545;
            }
            .menu__icon-menu,
            .menu__button--open .menu__icon-cross {
                display: block;
            }
            .menu__icon-cross,
            .menu__button--open .menu__icon-menu {
                display: none;
            }
            .container .btn {
                display: none;
            }
        }
        &__link {
            padding: 15px 30px;
        }
        &__button {
            align-self: center;
        }
        &__video-link {
            margin-top: 50px;
        }
    }
}

@media (max-width: 420px) {
    .header {
        &__title {
            font-size: 3rem;
            line-height: 3.5rem;
        }
    }
    .logo__image {
        max-width: 73%;
    }
    .menu {
        &__list .menu__list--open {
            padding: 35px 15px 100px 15px;
        }
    }
}
