.social {
    padding: 90px 0 100px;
    &__title {
        margin-bottom: 60px;
    }
    &__wrap {
        position: relative;
    }
    &__btn {
        position: absolute;
        right: 0;
        top: 0;
    }
    &__slider-wrapper {
        padding: 0 110px;
    }
    .slick-prev,
    .slick-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
    }
    .slick-prev {
        position: absolute;
        left: -100px;
    }
    .slick-next {
        position: absolute;
        right: -100px;
    }
}

@media (max-width: 735px) {
    .social {
        &__btn {
            position: relative;
            display: block;
            text-align: center;
            max-width: max-content;
            margin: 0 auto 20px;
        }
        &__slider-wrapper {
            padding: 0;
        }
    }
}
